/* TaskList.css */

.task-list {
    list-style-type: none;
    padding: 0;
}

.task-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
}

.task-item:last-child {
    border-bottom: none;
}

.task-title {
    font-size: 18px;
    color: #333;
}

.task-title.completed {
    text-decoration: line-through;
    color: #999;
}

.task-actions button {
    margin-left: 10px;
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.task-actions .edit-btn {
    background-color: #ffc107;
    color: #fff;
}

.task-actions .delete-btn {
    background-color: #dc3545;
    color: #fff;
}