/* TaskForm.css */

.task-form {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.task-form input,
.task-form textarea {
    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    width: 100%;
}

.task-form button {
    padding: 10px;
    margin-top: 10px;
    border: none;
    background-color: #28a745;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
}

.task-form button:hover {
    background-color: #218838;
}